<template>
    <div id="general-container">
        <div id="target-container">
            <div id="target-data">
                <div id="img-container">
                    <div class="inner-area" id="image-container" :style="{'--angle':angle,'--color1':color1,'--color2':color2}">
                        <div id="name-container"><div id="name-cont">{{getFirstLetter()}}</div></div>
                        <!-- <img src="https://picsum.photos/536/354" alt=""> -->
                    </div>
                </div>
                <p>{{ name }}</p>
                <p>{{ city}}</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'PresentationCard',
    props: {
        city:String,
        name:String,
        angle:String,
        color1:String,
        color2:String
    },
    methods:{
        getFirstLetter(){
            if(this.name != null && this.name != ""){
                return this.name.charAt(0).toUpperCase();
            }
            else{
                return "";
            }            
        }
    }
}
</script>
<style>
    #general-container{
        width:100%;
        padding-top:5%;
    }
    #target-container, #img-container{
        background: #ecf0f3;
        box-shadow: -3px -3px 7px #ffffff,
                     3px 3px 5px #ceced1;
    }
    #target-container{
        width:90%;
        max-width:350px;
        height:250px;
        margin:auto;
        background-color:#ecf0f3;
        text-align:center;
        border-radius:2%
    }
    #target-data{
        padding:10px 10px 10px 10px;
        color: #31344b;
    }
    #img-container{
        height: 130px;
        width: 130px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: auto;
    }
    #img-container .inner-area{
        height: calc(100% - 25px);
        width: calc(100% - 25px);
        border-radius: 50%;
         background: linear-gradient(var(--angle), var(--color1), var(--color2));
    }
    .inner-area img{
        height: 100%;
        width: 100%;
        border-radius: 50%;
        object-fit: cover;
    }
    .inner-area #name-container{
        height: 100%;
        width: 100%;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #ecf0f3;
        font-size: 40px;
    }
</style>