<template>
  <div id="app">
     <div>
      <TestComponent
      ref="tets"
      @showValues="showValues"
      @storeData="storeData"
      @overwriteVariables="overwriteVariables"
      :testMethodd="testMethodd"
      :testVar="testvar"
      />
    </div>
    <div id="data-container">
       <div id="card-container" v-for="item in dataBaseDisplayed"
        :key="item.id"
      >
        <PresentationCard
        :name="item.name" 
        :city="item.city"
        :angle="item.angle"
        :color1="item.color1"
        :color2="item.color2"
        />
      </div>
    </div>
  </div>
</template>

<script>
import TestComponent from './components/TestComponent.vue'
import PresentationCard from './components/PresentationCard.vue'


export default {
  name: 'App',
  components: {
    PresentationCard,
    TestComponent
  },
  data(){
    return{
      nameValue:"",
      cityValue:"",
      testvar:"",
      color1Var:"",
      color2Var:"",
      angleVar:"",
      dataBase:[{id:1,name:"Luis",city:"Gomez",angle:"90deg",color2:"#CB1F3B",color1:"#9753D3"}],
      dataBaseDisplayed:[],
      testVar:""
    }
  },
  methods:{
    storeData(){
      if(this.dataBase.length > 0 && !!this.nameValue && !!this.cityValue){
         let lastElemID = this.dataBase[this.dataBase.length - 1].id;
         this.generateRandomValues();
         let valueMap = {id:lastElemID+1,name:this.capitalizeFirstLetter(this.nameValue),city:this.capitalizeFirstLetter(this.cityValue),angle:this.angleVar,color2:this.color2Var,color1:this.color1Var};
         this.dataBase.push(valueMap);  
         if(this.dataBaseDisplayed.length > 0){
           this.dataBaseDisplayed.push(valueMap); 
         }    
         this.nameValue = this.cityValue = "";
         this.$refs.tets.nameValue = this.$refs.tets.cityValue = "";
      }
    },
    overwriteVariables(array){
      if(array.field == "city"){
        this.cityValue = array.cityValue || "";
      }
      else if(array.field == "name"){
        this.nameValue = array.nameValue || "";
      }
    },
    showValues(){
      if(this.dataBase.length > 0){
        // this.dataBase.forEach(element => {
        //   console.log(`ID: ${element.id} , NAME: ${element.name} , CITY: ${element.city}`);
        // });
        if(this.dataBaseDisplayed == 0){
          this.dataBaseDisplayed = this.dataBaseDisplayed.concat(this.dataBase);
        }
        else{
          this.dataBaseDisplayed = [];
        }
      }
    },
    capitalizeFirstLetter(string){
       return string.charAt(0).toUpperCase() + string.slice(1).toLowerCase();
    },
    generateRandomValues(){
      const randomHex = () =>{
        return Math.floor(Math.random() * 256)
          .toString(16)
          .padStart(2, "0");
      }
      const randomColor = () => `#${[...Array(3)].map(randomHex).join("")}`;
      const randomAngle = () => `${Math.floor(Math.random() * 361)}deg`;
      this.color1Var = randomColor();
      this.color2Var = randomColor();
      this.angleVar = randomAngle();
    },
    parentFunct(parameter){
      console.log(parameter);
    }
  },
  computed:{
    testMethodd(){
      return "shiiii"
    }
  }
}
</script>

